

















import { Component, Prop, Vue } from 'vue-property-decorator'

@Component({})
export default class MIcon extends Vue {
	@Prop({
		type: String
	})
	name!: string
	@Prop({
		type: String,
		default: '16px'
	})
	size!: string
	@Prop()
	color: string = ''

	externalClasses = ['icon']

	/**
	 * @LastEditors: chuyinlong
	 * @description: 点击反馈
	 */

	clickHandler() {
		this.$emit('click', {})
	}
}
